:root {
  --aside-size: 23rem;
  --cover-font-s: 16px;
  --cover-font-c: #fefefe;
  --cover-w: 300px;
  --cover-h: 300px;
  --cover-bg: #de4f45;
  --cover-pd: 30px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-color: #2b2b2b;
}

.wrapper {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template: 100% / var(--aside-size) 1fr;
  column-gap: 10px;
}

.aside {
  overflow-y: auto;
  background-color: #fefefe;
  box-shadow: 0 0 1px 0 rgba(21, 21, 21, 0.3);

  &__form {
    display: flex;
    flex-flow: column;
    justify-items: center;
    padding: 10px;

    &__select {
      margin-top: 25px !important;
    }
  }

  &__label {
    font-family: 'Roboto';
    font-weight: 200;
    font-size: 18px;
    text-transform: capitalize;
    margin: 20px 0 10px 5px;
  }
}

.main {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--bg);

  .cover {
    width: var(--cover-w);
    height: var(--cover-h);
    background-color: var(--cover-bg);
    box-shadow: 0 5px 10px -5px rgba(21, 21, 21, 0.6);
    transition: background-color 0.1s 0s linear;
    padding: var(--cover-pd);
    position: relative;
    overflow: hidden;

    &__wrapper {
      position: absolute;
      top: 0;
      left: 0;
      display: flex;
      justify-content: space-between;
      width: 100%;
      height: 100%;
      padding: inherit;
    }

    &__tunes {
      margin-left: 30px;
      margin-top: calc((var(--cover-h) - var(--cover-art-s)) / 2.7);
      font-size: var(--cover-font-s);
      font-family: 'Roboto';
      color: var(--cover-font-c);
      font-weight: 200;
      line-height: 1.75;
      text-shadow: 
        .2px .2px #fefefe,
        1px 1px rgba(21, 21, 21, 0.4),

        // 1.5px 1.5px rgba(21, 21, 21, 0.4),
        ;

      &__item {
        // background-color: var(--cover-bg);
      }
    }

    &__bg {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: calc(var(--cover-pd) * -1);
      margin-top: calc(var(--cover-pd) * -1);
      width: calc(var(--cover-w)*1.1);
      height: calc(var(--cover-h)*1.1);
      background-image: var(--cover-art);
      filter: blur(13px) contrast(0.8) saturate(0.9);
      transform: translateX(-5%);
      position: relative;

      &::after {
        content: "";
        display: block;
        position: absolute;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(21, 21, 21, 0.6) 90%);
      }
      // background-color: #fefefe;
    }

    &__art {
      margin: var(--cover-art-pos);
      width: var(--cover-art-s);
      height: var(--cover-art-s);
      object-fit: fill;
      box-shadow: 0 0 5px 0px rgba(21, 21, 21, 0.6);
    }
  }
}
